@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700;800;900&family=Source+Sans+Pro:wght@200;300;900&family=Teko&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{background-color:#141414;font-family:"Source Sans Pro",sans-serif;font-weight:400;color:#ffffff}body{font-family:"Source Sans Pro",sans-serif;font-weight:400;color:#ffffff}.App{background-color:#141414;font-family:"Source Sans Pro",sans-serif;font-weight:400;color:#ffffff}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.header{min-height:100px;align-content:center;text-align:center}.header img{vertical-align:center}.searchBar{text-align:center;align-items:center;justify-content:center;padding:0 0 50px}.form-control{color:#606060 !important;background-color:#1a1a1a !important;border:2px solid #606060 !important;border-radius:10px !important}.form-control:focus{color:#ffffff !important;background-color:#1a1a1a !important;border:2px solid #68CC9A !important;border-radius:10px;box-shadow:none !important}.image-grid-container{display:grid;grid-template-columns:auto auto auto;position:relative;max-width:770px}@media only screen and (max-width: 1440px){.image-grid-container{grid-template-columns:auto auto}}@media only screen and (max-width: 940px){.image-grid-container{grid-template-columns:auto}}.image-grid-container .searchResult{position:relative;width:100%;max-width:260px;max-height:410px;padding:5px}.image-grid-container .searchResult img{border-radius:3px;opacity:1;min-width:250px;min-height:400px;display:block;object-fit:cover;transition:0.5s ease;-webkit-backface-visibility:hidden;backface-visibility:hidden}.image-grid-container .searchResult .button{transition:0.5s ease;opacity:0;position:absolute;top:70%;left:10%;text-align:center}.image-grid-container .searchResult .button label input[type='checkbox']{display:none}.image-grid-container .searchResult .button .customCheckbox{position:relative;cursor:pointer}.image-grid-container .searchResult .button .customCheckbox .fa{color:#ffffff;position:absolute;font-size:25px}.image-grid-container .searchResult .button .customCheckbox .fa i .fal{color:gray}.image-grid-container .searchResult .button .customCheckbox .fa i .fas{opacity:0;transition:opacity 0.2s ease-in-out}.image-grid-container .searchResult .button .custom-checkbox input[type='checkbox']:checked ~ .fas{opacity:1}.image-grid-container .searchResult .title{transition:0.5s ease;opacity:0;position:absolute;top:80%;left:10%;color:#ffffff;font-weight:300;font-size:25px}.image-grid-container .searchResult .title p{font-size:14px}.image-grid-container .searchResult:hover img{opacity:0.3}.image-grid-container .searchResult:hover .button{opacity:1}.image-grid-container .searchResult:hover .title{opacity:1}.nominationTable{color:#ffffff;position:-webkit-sticky;position:sticky;top:50px}.nominationTable p{font-family:sans-serif;font-weight:900}.nominationTable .nomination p{font-weight:400;margin:0;display:inline !important}

